import { Component, NgZone, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ApiservicesService } from '../../provider/apiservices.service';
import { DataTransferService } from '../../services/data-transfer.service';
import { LocalStorageService } from '../../services/local-storage.service';
import { URLS, APP_CONFIG } from '../../../../src/assets/constant';

@Component({
  selector: 'app-redeem-voucher-category-list',
  templateUrl: './redeem-voucher-category-list.page.html',
  styleUrls: ['./redeem-voucher-category-list.page.scss'],
})

export class RedeemVoucherCategoryListPage implements OnInit{

  categoryData:any;
  categoryListData:any;
  errorMessage:any="";
  value:number=0;
  myinfiniteScrollCall:boolean=false;
  myInfiniteScroll:any;
  // showErrorImage:any="";
  // no_data_message:any="";

  constructor(private router:Router,private storageService:LocalStorageService,private apiservices:ApiservicesService,
    private sendData:DataTransferService,private zone:NgZone) {

  }









  ngOnInit(){
    this.storageService.getStorage().then(res=>{
      this.redeemHomeDetail();
      console.log("ionic lifecycle called getStorage==>",res);
    });
  }

  redeemHomeDetail(){
    this.zone.run(()=>{
      console.log("redeem home page function==");
      let apiKey={
        "value":this.value
      };
      console.log("redeem home page api key", apiKey);
      this.apiservices.apiCallWithLoginToken(URLS.redeemCategoryList, apiKey).subscribe((result) => {
        console.log("redeem home page Api Result==", result);
        this.categoryData=result.data;
        if(result.status==1 || result.status=='1'){
          if(this.categoryListData==undefined){
            this.categoryListData=result.data.categories;
            console.log("noticeBoard List Infinite Scroll if1==", this.categoryListData);
          }else{
            this.categoryListData=this.categoryListData.concat(result.data.categories);
            console.log("noticeBoard List Infinite Scroll else1==", this.categoryListData);
          }
          if(this.myinfiniteScrollCall==true){
            console.log("noticeBoard List Infinite Scroll if2==", this.categoryListData);
            this.myInfiniteScroll.target.complete();
            this.errorMessage = "";
            this.myinfiniteScrollCall=false;
          }
          console.log("thoughtOftheDayApi List data1==", this.categoryListData);
          console.log("thoughtOftheDayApi List data2==", this.errorMessage);
        }else{
          this.errorMessage = result.message;
          // this.no_data_message=result.data.settings.no_data_msg;
          // this.showErrorImage=result.data.settings.no_data_image;
          console.log("noticeBoard List Infinite Scroll12==", this.categoryListData);
          if(this.myinfiniteScrollCall==true){
            console.log("noticeBoard List Infinite Scroll123==", this.categoryListData);
            this.myInfiniteScroll.target.complete();
            this.myinfiniteScrollCall=false;
          }
        }
      }, (err)=>{
        console.log("last array of account statement==", err);
        this.apiservices.showToastMessage(JSON.stringify(err), 'top', 3000, 'redBg');
      });
    });
  }

  voucherList(categoryClickData){
    this.zone.run(()=>{
      this.sendData.alldata=categoryClickData;
      this.router.navigate(["/voucher-list"]);
    });
  }

  doInfinite(infiniteScroll){
    this.myinfiniteScrollCall=true;
    console.log("Begin async operation 1==");
    setTimeout(() => {
      this.value=this.categoryListData.length;
      console.log("Begin async operation 2==",this.value);
      this.myInfiniteScroll = infiniteScroll;
      this.redeemHomeDetail();
    }, 500);
  }








  
}